@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,800&display=swap);
:root {
  --color-surface: white;
  --color-solid: black;
  --color-primary: teal;
  --color-gray-1: #333333;
  --color-gray-6: #f2f2f2;
  --color-pastelle-pink: #e5c9c6;
  --color-pastelle-brown: #a7a195;
  --color-pastelle-aqua: #01564e;
  --color-pastelle-blue: #7e8fa9;
  --color-dark-purple: #1b1044;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: Open Sans, sans-serif;
  font-style: normal;
  font-weight: normal;
  background-color: #333333;
  background-color: var(--color-gray-1);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: var(--color-gray-1);
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

h1 {
  font-family: Open Sans, sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 24px;
  line-height: 33px;
  display: flex;
  align-items: center;
}

p {
  text-align: justify;
  font-size: 14px;
  line-height: 19px;
}

.Home {
  background-color: #282c34;
}

